@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.left{
  background: white;
  border-radius: 10px;
}

.left img{
  background: white;
}

.iconbox{
  height: 40px;
}

.iconboxmobile{
  height: 40px;
  display: none;
}

.picker{
  position:absolute;
  bottom: 50px;
  left:20px;
}

.keyboard{
  display: flex;
  background: white;
  border-radius: 10px;
  cursor: pointer;
  position: relative;
}

.title{
  font-size: 15px;
  overflow: hidden;
}

.mobile{
  display: none;
  background: white;
  border-radius: 10px;
  margin-bottom: 30px;
}

@media (max-width: 770px) {
  .container {
    display: block;
  }
  .left {
    display: none;
  }
  .mobile{
    display: block;
  }
  .iconbox{
    display: none;
  }
  .iconboxmobile{
    display: block;
  }
}
.App {
  min-height: 100vh;
  display: flex;
  background-image: url(/static/media/bg_w.ecbd00d2.webp);
  background-position: center;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.container{
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center;
  width: 100%;
}

.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.container1 p {
  font-size: 0.8rem;
  padding: 0.5rem;
  padding-top: 0rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  text-align: center;
  overflow: hidden;
  
}
.container1 p span.typed-text {
  font-weight: 400;
  color: darkgreen;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
}
.container1 p span.cursor {
  display: inline-block;
  background-color: #ccc;
  margin-left: 0.1rem;
  width: 3px;
  animation: blink 1s infinite;
}
.container1 p span.cursor.typing {
  animation: none;
}
@keyframes blink {
  0%  { background-color: #ccc; }
  49% { background-color: #ccc; }
  50% { background-color: transparent; }
  99% { background-color: transparent; }
  100%  { background-color: #ccc; }
}
.messages {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: none;
}

